.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.list {
  font-family: Mill;
}

.list span {
  color: dodgerblue;
}
.modal {
  width: 100%;
  height: 250px;
  background-color: rgb(112, 128, 144, 0.6);
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.menubar {
  display: flex;
  background:linear-gradient(45deg, lightblue , coral) ;
  width:100%;
  color: white;
  padding-left: 20px;
}
div {
  box-sizing: border-box;
}

.list {
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid slategray;
}

.modal {
  background-color: lightgrey;
  padding: 15px;
  text-align: left;
  width: 100%;
}


@font-face {
  font-family: 'Mill';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2206-02@1.0/MYHaemalgeunSangsang.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}